// Kanban Board Demo
// =============================================================

const boardDemo = {

  init () {

    this.bindUIActions()

  },

  bindUIActions () {

    // event handlers
    this.handleBoardStyle()
    this.handleSortable()
  },

  byId (id) {
    return document.getElementById(id)
  },

  handleBoardStyle () {
    $(document).on('mouseenter mouseleave', '.task-header', function (e) {
      const isHover = e.type === 'mouseenter'
      $(this).parent().toggleClass('hover', isHover)
    })
  },

  handleSortable () {
    const board = this.byId('board')
    // Multi groups
    Sortable.create(board, {
      animation: 150,
      draggable: '.tasks',
      handle: '.task-header',
      filter: '.ignore-sort',
      delay: 100,
      forceFallback: true
    })

    ;[].forEach.call(board.querySelectorAll('.task-body'), function (el) {
      Sortable.create(el, {
        group: 'tasks',
        animation: 150,
        filter: '.ignore-sort',
        delay: 100,
        forceFallback: true
      })
    })
  }
}

boardDemo.init()
